
import { defineComponent, ref, Ref } from "vue";
import CheerStick from "@/components/CheerStick.vue";
import Background from "@/components/BackGround.vue";
import GoogleModal from "@/components/GoogleLoginModal.vue";

// import Settings from "@/components/SettingsDropdown.vue";
interface NotificationComponent {
  showNotification(message: string): void;
}

export default defineComponent({
  components: {
    CheerStick,
    Background,
    GoogleModal,
  },
  data() {
    return {
      level: 1,
      state: 1,
      type: "stick",
      showGModal: true,
      GModalContent: "Google Register!!",
    };
  },
  mounted() {
    // eslint-disable-next-line prettier/prettier
    const notificationComponent = this.$refs.notification as Ref<NotificationComponent>;
    notificationComponent.value?.showNotification(this.GModalContent);
    setInterval(() => {
      this.state++;
      if (this.state > 10) {
        this.state = 1;
      }
      console.log(this.state + " " + this.level);
    }, 1000);
  },
  methods: {
    hideNotification() {
      this.showGModal = false;
    },
  },
});
